<template>
<u-multi-select :data="options" v-model="value"></u-multi-select>
</template>

<script>
export default {
    data() {
        return {
            value: [],
            options: [{
                value: '1',
                text: '',
            }, {
                value: '2',
                text: ''
            }, {
                value: '3',
                text: ''
            }, {
                value: '4',
                text: ''
            }, {
                value: '5',
                text: '',
            }],
        };
    },
    watch: {
        value(newValue) {
            console.log(newValue);
        }
    }
};
</script>



