<template>
<u-multi-select filter :data="options" v-model="value"></u-multi-select>
</template>

<script>
export default {
    data() {
        return {
            value: [],
            options: [{
                value: '1',
                text: ''
            }, {
                value: '2',
                text: ''
            }, {
                value: '3',
                text: ''
            }, {
                value: '4',
                text: ''
            }, {
                value: '5',
                text: '',
            },{
                value: '6',
                text: '',
            },{
                value: '7',
                text: '',
            },{
                value: '8',
                text: '',
            },{
                value: '9',
                text: '',
            }],
        };
    }
};
</script>



